<template>
  <div id="app">

    <FrameWork v-bind:msg="msg" v-bind:title="title" v-bind:footer="footer">
      <template slot="header">
        <div>sss</div>
      </template>

      <template slot="content">
        <Card class="my-card">
          <div style="text-align:center">
            <img src="logo.png">
            <h3>A high quality UI Toolkit based on Vue.js</h3>
          </div>
        </Card>

        <Button type="primary" @click="getGankDatas">点击</Button>
        <ul>
          <li v-for="gank in ganks" :key="gank._id">
            {{gank.desc}}
          </li>
        </ul>

        <Modal v-model="modal1" title="Common Modal dialog box title" @on-ok="ok" @on-cancel="cancel">
          <p>Content of dialog</p>
          <p>Content of dialog</p>
          <p>Content of dialog</p>
        </Modal>
      </template>

      <template slot="footer">
      </template>

    </FrameWork>
  </div>
</template>

<script>
import FrameWork from "./components/FrameWork";

export default {
  name: "app",
  components: {
    FrameWork
  },
  data() {
    return {
      modal1: false,
      msg: "我是自定义页面框架",
      title: "我是标题",
      footer: "我是foot",
      ganks: []
    };
  },
  methods: {
    ok() {
      this.$Message.info("Clicked ok");
    },
    cancel() {
      this.$Message.info("Clicked cancel");
    },
    getGankDatas() {
      let vm = this;
      vm.$http
        .get(`${this.HOST}/api/data/Android/10/1`)
        .then(function(response) {
          let data = response.data
          if (!data.error) {
            console.log(data.results)
            console.log(vm)
            vm.ganks = data.results
          }
        })
        .catch(function(error) {
          console.log(error)
        })
    }
  }
};
</script>

